/* Smartphones (portrait and landscape) ----------- */
@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
    .wrapper {
        padding: 0 15px;
        margin-top: 50px;
    }
    .Header {
        &__inner {
            display: flex;
            align-items: center;
            justify-content: center;
        }
        &__logo {
            margin: 0 auto;
        }
        &__nav {
            li {
                margin: 0 11px;
                a {
                    font-size: 16px;
                }
            }
        }
    }
    .SignIn {
        &__form {
            margin-left: auto;
            input,
            button {
                padding: 20px;
                margin: 10px 0;
            }
            input {
                &::placeholder {
                    font-size: 16px;
                }
            }
        }
        &__youtube {
            &-btn {
                padding: 20px;
            }
            &-span {
                font-size: 16px;
            }
        }
    }
    .Cpa {
        font-size: 16px;
        &__item {
            width: 80%;
            &-img {
            }
        }
    }
    .Payouts {
        width: 100%;
        font-size: 16px;
    }
    .Account {
        width: 100%;
        font-size: 16px;
        &__form {
            input {
                &::placeholder {
                    font-size: 16px;
                }
            }
            button {
                font-size: 16px;
            }
        }
    }
    .Statistics {
        font-size: 16px;
        &__inner {
            flex-wrap: wrap;
        }
        &__views,
        &__month {
            padding: 32px 40px;
            width: 100%;
        }
    }
    .Main {
        &__charts {
            padding: 5px;
        }
    }
    .LineChart, .Bar {
        &__top {
            font-size: 16px;
            &-month {
                padding: 10px;
                min-width: 160px;
            }
            ul {
                right: 0;
                max-height: 200px;
                overflow: auto;
                border-radius: 10px;
                li {
                    cursor: pointer;
                    min-width: 160px;
                    background-color: #1b1b1b;
                    padding: 15px 0;
                    &:hover {
                        background-color: lighten(#1b1b1b, 10);
                    }
                }
            }
        }
        &__main {
            height: 300px;
            padding: 10px 0;
        }
    }
}

