.Cpa {
    &__inner {
    }

    &__items {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }
    &__item {
        width: calc(80% / 3);
        background-color: rgba(black, 0.8);
        border-radius: 10px;
        overflow: hidden;
        margin: 10px;
        &-inner {
            padding: 34px 44px;
        }
        &-title {
            color: white;
            margin-bottom: 18px;
        }
        &-date {
            font-size: 16px;
            letter-spacing: 0.5px;
            margin-bottom: 20px;
        }
        &-text {
            font-style: italic;
            line-height: 38px;
            font-size: 16px;
            margin-bottom: 24px;
        }
        &-price {
            text-align: right;
            font-family: Baloo;
            font-size: 42px;
            line-height: 38px;
            letter-spacing: 0.5px;
            color: rgba(94, 255, 226, 0.9);
        }
    }
}
